body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

div {
  background-color: antiquewhite;
}
h1,
h2 {
  font-family: "Courier New", Courier, monospace;
}

button {
  background-color: lightsalmon;
  font-family: "Courier New", Courier, monospace;
}

.error {
  color: red;
  background: lightgray;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.info {
  color: green;
  background: wheat;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
